import React from 'react';
import AliTable from './components/AliTable';
import { ContextMenu, MenuItem, ContextMenuTrigger, connectMenu } from 'react-contextmenu';
import PropTypes from 'prop-types';
import DynamicMenu from './menu';
import AlarmStatic from './toolbar/AlarmStatic';
import AlarmSeverity from './render/AlarmSeverity';
import Modal from './components/modal';
import CheckBoxGroup from './components/gcheckbox';
import { exportExcel } from './util/exportExcel';
import { cloneArray, cloneDeep } from './util/utils';
import { toTree, staticAlarmLevels, filterDefault, filterByCondition, convertColumnToFilter } from './util/AlarmUtil';
import './style.less';
const renderStrategy = {
  severity: AlarmSeverity,
};
const ConnectedMenu = connectMenu('same_unique_identifier123')(DynamicMenu);
class Table extends React.Component {
  constructor (props) {
    super(props);
    this.onExport.bind(this);
    this.alarmRender.bind(this);
    this.onHotEvent.bind(this);
    this.onModalClose.bind(this);
    this.onFilterChange.bind(this);
    this.onRealFilter.bind(this);
    this.filterTempData = {};
    this.configColumnTemp = null;
  }
	state = {
	  showFilter: false,
	  showConfigColumn: false,
	  configColumn: [],
	  filterMap: {},
	};
	onExport = () => {
	  exportExcel(this.props.columns, this.props.data, 'æ´»å¨åè­¦.xlsx', 'code', 'title');
	};
	alarmRender (columns, alarmLevels) {
	  columns.forEach((col) => {
	    if (renderStrategy[col.code] && !col.render) {
	      col.render = (value, record, rowIndex) => {
	        let RenderComponent = renderStrategy[col.code];
	        return <RenderComponent value={value} record={record} rowIndex={rowIndex} levels={alarmLevels}></RenderComponent>;
	      };
	    }
	  });
	}
	onHotEvent = (type) => {
	  switch (type) {
	  case 'filter':
	    if (!this.state.showFilter) {
	      this.setState({
	        showFilter: true,
	      });
	    }
	    break;
	  case 'unfilter':
	    this.onCancelFilter();
	    break;
	  case 'configColumn':
	    if (!this.state.showConfigColumn) {
	      this.setState({
	        showConfigColumn: true,
	      });
	    }
	    break;
	  }
	};
	onModalClose = () => {
	  this.filterTempData = {};
	  this.setState({
	    showFilter: false,
	  });
	};
	onConfigColumnClose = () => {
	  this.setState({
	    showConfigColumn: false,
	  });
	};
	onFilterChange = (value) => {
	  this.filterTempData[value.field] = value.selects;
	};
	onConfigColumnChange = (value) => {
	  this.configColumnTemp = value.selects;
	};
	onRealConfigColumn = () => {
	  this.setState(
	    {
	      configColumn: this.configColumnTemp,
	      showConfigColumn: false,
	    },
	    () => {
	      this.configColumnTemp = [];
	    }
	  );
	};
	onRealFilter = () => {
	  let filterMap = {};
	  Object.assign(filterMap, this.state.filterMap);
	  Object.keys(this.filterTempData).forEach((key) => {
	    filterMap[key] = this.filterTempData[key];
	  });
	  this.filterTempData = {};
	  this.setState({
	    filterMap: cloneDeep(filterMap),
	    showFilter: false,
	  });
	};
	onCancelFilter = () => {
	  this.setState({
	    filterMap: {},
	  });
	};
	onMenuClick = (evt, data) => {
	  if (this.props.events && this.props.events.onMenuEvent) {
	    this.props.events.onMenuEvent(evt, data);
	  }
	};
	render () {
	  let { columns, data, expandKey, menus, staticLabel, events, filters, alarmLevels } = this.props;
	  let { showFilter, showConfigColumn, filterMap, configColumn } = this.state;
	  let cColumns = convertColumnToFilter(configColumn, columns, this.configColumnTemp);
	  let newData = filterByCondition(filterMap, data);
	  let levels = staticAlarmLevels(newData, alarmLevels);
	  let treeData = toTree(cloneDeep(newData));
	  this.alarmRender(cColumns.columns, alarmLevels);
	  console.log('filterMap---->>%o', filterMap);
	  return (
	    <div className="table-wrap">
	      <AlarmStatic
	        levels={levels}
	        label={staticLabel}
	        exportEvent={this.onExport}
	        events={this.onHotEvent}
	      ></AlarmStatic>
	      <div className="table-content">
	        <ContextMenuTrigger id="same_unique_identifier123" collect={(props) => props} onItemClick={() => {}}>
	          <AliTable columns={cColumns.columns} data={treeData} expandKey={expandKey} events={events} />
	        </ContextMenuTrigger>
	        <ConnectedMenu menus={menus} />
	      </div>
	      <Modal visible={showFilter} onClose={this.onModalClose} onOk={this.onRealFilter} title="è¿æ»¤åè­¦">
	        <div className="filter-modal">
	          {filters.map((filter) => (
	            <CheckBoxGroup
	              onSelectedChange={this.onFilterChange}
	              key={filter.field}
	              data={filter.items}
	              groupName={filter.name}
	              field={filter.field}
	              selects={filterMap[filter.field] ? filterMap[filter.field].slice(0) : []}
	            ></CheckBoxGroup>
	          ))}
	        </div>
	      </Modal>
	      <Modal
	        visible={showConfigColumn}
	        onClose={this.onConfigColumnClose}
	        onOk={this.onRealConfigColumn}
	        title="æ¾ç¤ºåéç½®"
	      >
	        <div className="columns-modal">
	          <CheckBoxGroup
	            onSelectedChange={this.onConfigColumnChange}
	            key="showConfig"
	            data={cColumns.configColumns}
	            groupName="å¨é"
	            field="showConfig"
	            direction="v"
	            selects={cColumns.selecteds}
	          ></CheckBoxGroup>
	        </div>
	      </Modal>
	    </div>
	  );
	}
}
Table.propTypes = {
  data: PropTypes.array.isRequired,
  columns: PropTypes.array.isRequired,
  expandKey: PropTypes.string.isRequired,
  alarmLevels: PropTypes.object,
  filters: PropTypes.array,
  menus: PropTypes.array,
  events: PropTypes.shape({
    onClickEvent: PropTypes.func,
    onMouseEvent: PropTypes.func,
    onSelectEvent: PropTypes.func,
    onMenuEvent: PropTypes.func,
  }),
  staticLabel: PropTypes.string,
};
Table.defaultProps = {
  data: [],
  columns: [],
  alarmLevels: {
    level1: {
      label: 'ä¸çº§åè­¦',
	  value: '1',
	  color: '#ff6464'
    },
    level2: {
      label: 'äºçº§åè­¦',
	  value: '2',
	  color: '#ffa85a'
    },
    level3: {
      label: 'ä¸çº§åè­¦',
	  value: '3',
	  color: '#ffec6f'
    },
    level4: {
      label: 'åçº§åè­¦',
	  value: '4',
	  color: '#8bbcfe'
    },
    level5: {
      label: 'äºçº§åè­¦',
	  value: '5',
	  color: '#8bbddd'
    },
  },
  events: {
    onClickEvent: null,
    onMouseEvent: null,
    onSelectEvent: null,
    onMenuEvent: null,
  },
  expandKey: 'alarmTitle',
  menus: [],
  staticLabel: 'æ´»å¨åè­¦',
  filters: filterDefault,
};
export default Table;
