import React, { useState } from 'react';
import { BaseTable, Classes, useTablePipeline, features, collectNodes, applyTransforms } from 'ali-react-table';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { debounce } from '../util/utils';

const HippoBaseTable = styled(BaseTable)`
	--row-height: 50px;
	--header-row-height: 36px;
	--border-color: #eee;
	--lock-shadow: rgba(56, 88, 207, 0.12) 0 0 8px 4px;
	--cell-padding: 8px 20px;
	--line-height: 20px;
	--color: #666;
	--header-color: #333;
	--header-bgcolor: #f9f9f9;
	--hover-bgcolor: #eff3fb;
	--header-hover-bgcolor: #eff2fb;
	--highlight-bgcolor: #e2e6ef;
	--header-highlight-bgcolor: #e2e6ef;
	&.header-depth-1 {
		--header-row-height: 40px;
	}
	&.header-depth-0 {
		--header-row-height: 50px;
	}
	&:not(.bordered) {
		--cell-border-vertical: none;
		--header-cell-border-vertical: none;
		thead > tr.first th {
			border-top: none;
		}
		thead > tr.last th {
			border-bottom: none;
		}
	}
	th {
		font-weight: 500;
	}
	&.compact {
		--row-height: 40px;
		--header-row-height: 40px;
		--cell-padding: 8px 16px;
	}
	&.zebra tr.odd,
	tr.alternative {
		--bgcolor: #f9f9f9;
	}
`;
const CheckBox = (props) => {
  let { checked, onChange } = props;
  return <input type="checkbox" checked={checked} onClick={onChange}></input>;
};
const AliTable = (props) => {
  let { data, columns, events } = props;
  const [value, onChange] = useState([]);
  const pipeline = useTablePipeline({ components: { Checkbox: CheckBox } })
    .input({ dataSource: data, columns: columns })
    .primaryKey('alarmId')
    .snapshot('flat')
    .use(
      features.sort({
        mode: 'single',
        highlightColumnWhenActive: false,
      })
    )
    .use(features.treeMode({}))
    .use(
      features.treeSelect({
        tree: data,
        rootKey: 'alarmId',
        value,
        onChange,
        highlightRowWhenSelected: true,
        checkboxColumn: { lock: false },
        clickArea: 'cell',
      })
    );
  let timer = 0;
  return (
    <HippoBaseTable
      {...pipeline.getProps()}
      useVirtual
      style={{ height: '100%', overflow: 'auto' }}
      getRowProps={(record, rowIndex) => ({
        onClick (evt) {
          clearTimeout(timer);
          timer = setTimeout(() => {
            console.log('onClick record---->>%o,%o', record, evt);
            if (events['onClickEvent']) {
              events['onClickEvent'](evt, record, 'onClick');
            }
          }, 200);
        },
        onDoubleClick (evt) {
          clearTimeout(timer);
          console.log('onClick onDoubleClick---->>%o', record);
          if (events['onClickEvent']) {
            events['onClickEvent'](evt, record, 'onDoubleClick');
          }
        },
        onMouseEnter (evt) {
          debounce(() => {
            console.log('onClick onMouseenter---->>%o', record);
            if (events['onMouseEvent']) {
              events['onMouseEvent'](evt, record, 'onMouseEnter');
            }
          }, 200)();
        },
        onMouseLeave (evt) {
          debounce(() => {
            if (events['onMouseEvent']) {
              events['onMouseEvent'](evt, record, 'onMouseLeave');
            }
          }, 200)();
        },
      })}
    />
  );
};
AliTable.propTypes = {
  data: PropTypes.array.isRequired,
  columns: PropTypes.array.isRequired,
  expandKey: PropTypes.string.isRequired,
  events: PropTypes.shape({
    onClickEvent: PropTypes.func,
    onMouseEvent: PropTypes.func,
    onSelectEvent: PropTypes.func,
    onMenuEvent: PropTypes.func,
  }),
};
AliTable.defaultProps = {
  data: [],
  columns: [],
  events: {
    onClickEvent: null,
    onMouseEvent: null,
    onSelectEvent: null,
    onMenuEvent: null,
  },
  expandKey: 'alarmTitle',
};
export default AliTable;
