const toTree = (data) => {
  let map = new Map();
  const result = [];
  data.forEach((item, index) => {
    if (!item.parentIds) {
      map.set(item.alarmId, item);
      result.push(item);
    }
  });
  let cache = new Map();
  data.forEach((item) => {
    if (item.parentIds) {
      let fund = item.parentIds.split(',').every((pid) => {
        if (map.has(pid)) {
          const parent = map.get(pid);
          let childrens = [];
          if (parent.hasOwnProperty('children')) {
            childrens = parent['children'] || [];
          }
          let isAdd = false;
          if (!cache.has(item.alarmId + pid)) {
            isAdd = true;
            cache.set(item.alarmId + pid, item.alarmId + pid);
          }
          if (isAdd) {
            if (!parent.hasOwnProperty('children')) {
              parent['children'] = [];
            }
            parent['children'].push(item);
          }
          return true;
        } else {
          return false;
        }
      });
      if (!fund) {
        result.push(item);
      }
    }
  });
  return result;
};
const staticAlarmLevels = (data, alarmLevels) => {
  let levels = {
    level1: 0,
    level2: 0,
    level3: 0,
    level4: 0,
    total: 0,
  };
  for (let i = 0, num = data.length; i < num; i++) {
    let alarm = data[i];
    if (alarm.severity == alarmLevels.level1.value) {
      levels.level1++;
    } else if (alarm.severity == alarmLevels.level2.value) {
      levels.level2++;
    } else if (alarm.severity == alarmLevels.level3.value) {
      levels.level3++;
    } else if (alarm.severity == alarmLevels.level4.value) {
      levels.level4++;
    }
    levels.total++;
  }
  return levels;
};
