import XLSX from 'xlsx';

/**
 * åsheetå¯¼åº
 * @param {*} headers è¡¨å¤´ä¿¡æ¯
 * @param {*} data è¡¨æ ¼æ°æ®
 * @param {*} fileName æä»¶åå­
 * @param {*} dkey è¡¨å¤´key
 * @param {*} label è¡¨å¤´label
 */
function exportExcel (headers, data, fileName, dkey, label) {
  console.log('headers---->>%o,%o', headers, data.length);
  let results = headers.filter((h) => {
    if (h.hasOwnProperty(dkey)) {
      return true;
    }
    return false;
  });
  const _headers = results
    .map((item, i) => Object.assign({}, { key: dkey, title: item[label], position: String.fromCharCode(65 + i) + 1 }))
    .reduce((prev, next) => {
      console.log('reduce---->>%o', next);
      return Object.assign({}, prev, { [next.position]: { key: next.key, v: next.title } });
    }, {});

  const _data = data
    .map((item, i) =>
      results.map((key, j) =>
        Object.assign({}, { content: item[key[dkey]], position: String.fromCharCode(65 + j) + (i + 2) })
      )
    )
  // å¯¹åæçç»æè¿è¡éç»´å¤çï¼äºç»´æ°ç»åæä¸ç»´æ°ç»ï¼
    .reduce((prev, next) => prev.concat(next))
  // è½¬æ¢æ worksheet éè¦çç»æ
    .reduce((prev, next) => Object.assign({}, prev, { [next.position]: { v: next.content } }), {});
  // åå¹¶ headers å data
  const output = Object.assign({}, _headers, _data);
  // è·åææååæ ¼çä½ç½®
  const outputPos = Object.keys(output);
  // è®¡ç®åºèå´ ,["A1",..., "H2"]
  const ref = `${outputPos[0]}:${outputPos[outputPos.length - 1]}`;

  // æå»º workbook å¯¹è±¡
  const wb = {
    SheetNames: ['mySheet'],
    Sheets: {
      mySheet: Object.assign({}, output, {
        '!ref': ref,
        '!cols': [
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
          { wpx: 200 },
        ],
      }),
    },
  };

  // å¯¼åº Excel
  XLSX.writeFile(wb, fileName);
}
