import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import './style.less';
const Modal = (props) => {
  let { visible, onClose, onOk, okTxt, cancelTxt, showFooter, footer, title } = props;
  function close () {
    if (onClose) {
      onClose.call(null);
    }
  }
  function ok () {
    if (onOk) {
      onOk.call(null);
    }
  }
  return (
    <>
      {visible
        ? ReactDOM.createPortal(
          <div className="tb-modal-wrap">
            <div className="tb-modal-mask"></div>
            <div className={`tb-modal-container ${visible ? 'animation-zoomin' : 'animation-zoomout'}`}>
              <div className="m-header">
                <div className="title">{title}</div>
                <div className="close" onClick={close}></div>
              </div>
              <div className="m-content">{props.children}</div>
              {showFooter ? (
                <div className="m-footer">
                  {footer ? (
                    footer
                  ) : (
                    <>
                      <span className="cancel btn" onClick={close}>
                        {cancelTxt}
                      </span>
                      <span className="ok btn" onClick={ok}>{okTxt}</span>
                    </>
                  )}
                </div>
              ) : null}
            </div>
          </div>,
          document.body
				  )
        : null}
    </>
  );
};
Modal.propTypes = {
  visible: PropTypes.bool.isRequired,
  title: PropTypes.string,
  onClose: PropTypes.func,
  onOk: PropTypes.func,
  okTxt: PropTypes.string,
  cancelTxt: PropTypes.string,
  showFooter: PropTypes.bool,
  footer: PropTypes.element,
};
Modal.defaultProps = {
  visible: false,
  title: 'å¼¹åºæ¡',
  onClose: null,
  onOk: null,
  okTxt: 'ç¡®å®',
  cancelTxt: 'åæ¶',
  showFooter: true,
  footer: null,
};
export default Modal;
