import React, { useState } from 'react';
import PropTypes from 'prop-types';
import uuid from '../../util/UUID';
import CheckBox from './CheckBox';
import './style.less';
const CheckBoxGroup = (props) => {
  let { groupName, data, selects, direction, onSelectedChange, field } = props;
  let [selecteds, setSelecteds] = useState(selects);
  let [alls, setAlls] = useState({
    name: groupName,
    value: groupName,
    checked: (selecteds.length === data.length && data.length != 0),
  });
  if (selecteds) {
    data.forEach((d) => {
      if (selecteds.indexOf(d.value) != -1) {
        d.checked = true;
      } else {
        d.checked = false;
      }
    });
  }
  function onChange (e) {
    let index = selecteds.indexOf(e.value);
    if (index == -1) {
      selecteds.push(e.value);
    } else {
      selecteds.splice(index, 1);
    }
    setSelecteds(selecteds);
    let result = { ...alls };
    if (selecteds.length === data.length && selecteds.length) {
      result.checked = true;
    } else {
      result.checked = false;
    }
    setAlls(result);
    onEmitChange(selecteds);
  }
  function onCheckAll (item) {
    let result = { ...item };
    result.checked = !result.checked;
    setAlls(result);
    let sac = [];
    if (result.checked) {
      data.forEach((d) => {
        sac.push(d.value);
      });
    }
    setSelecteds(sac);
    onEmitChange(sac);
  }
  function onEmitChange (data) {
    if (onSelectedChange) {
      onSelectedChange.call(null, {
        group: groupName,
        selects: data,
        field
      });
    }
  }
  return (
    <div className="checkgroup-wrap">
      <div className="all-checkbox">
        <CheckBox
          value={alls}
          onChange={() => {
            onCheckAll(alls);
          }}
        ></CheckBox>
      </div>
      <div className={direction == 'h' ? 'h-layout' : 'v-layout'}>
        {data.map((item) => (
          <CheckBox
            value={item}
            key={uuid(8)}
            onChange={() => {
              onChange(item);
            }}
          ></CheckBox>
        ))}
      </div>
    </div>
  );
};
CheckBoxGroup.propTypes = {
  groupName: PropTypes.string,
  field: PropTypes.string,
  data: PropTypes.array.isRequired,
  selects: PropTypes.array,
  direction: PropTypes.string,
  onSelectedChange: PropTypes.func
};
CheckBoxGroup.defaultProps = {
  groupName: 'åç»',
  field: 'key',
  data: [
    {
      name: 'åç»1',
      value: 'åç»1',
    },
    {
      name: 'åç»2',
      value: 'åç»2',
    },
    {
      name: 'åç»3',
      value: 'åç»3',
    },
  ],
  selects: [],
  direction: 'h',
  onSelectedChange: null
};
export default CheckBoxGroup;
