import React from 'react';
import PropTypes from 'prop-types';
const AlarmSeverity = (props) => {
  const { value, record, rowIndex, levels } = props;
  console.log('AlarmSeverity---->>%o', props);

  let level = value;
  let label = '';
  let color = ''
  if (level == levels.level1.value) {
    color = '#ff6464';
    label = levels.level1.label;
  } else if (level == levels.level2.value) {
    color = '#ffa85a';
    label = levels.level2.label;
  } else if (level == levels.level3.value) {
    color = '#ffec6f';
    label = levels.level3.label;
  } else if (level == levels.level4.value) {
    color = '#8bbcfe';
    label = levels.level4.label;
  }
  return color == '' ? null : <div style={{background: color, padding: '5px 10px', textAlign: 'center'}}>{label}</div>;
}
AlarmSeverity.propTypes = {
}

export default AlarmSeverity;
